import {WinJSDec, fnError} from './APP/config';

var name = 'CUSTOMER_EVENT_SEL';
var CUSTOMER_EVENT_SEL = (fn = _ => {
}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      document.addEventListener('CHARTBAR_EVENT_SEL', fn);
    })
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default CUSTOMER_EVENT_SEL;
